<script lang="ts">
	import { createEventDispatcher } from "svelte";
	import * as d3 from "d3";

	import type { Race, ScatterInteractDetails, ScatterInteractEventMap } from "+/modules/types";

	import { months } from "+/modules/shared";
	import { createChart } from "+/modules/charting/createChart";
	import { getDateTickWidth } from "+/modules/charting/getDateTickWidth";

	export let races: Race[];

	const width = 768,
		height = 64,
		top = 0,
		left = 0,
		right = 0,
		bottom = 96,
		gap = 16,
		scatterRadius = 4,
		msInWeek = 1000 * 60 * 60 * 24 * 7,
		yScale = d3.scaleLinear()
			.domain([0.0, 1.0])
			.range([0, height]),
		scatterY = yScale(0.66),
		bisectDate = d3.bisector<Race, Date>(d => d.when).center,
		dispatch = createEventDispatcher<ScatterInteractEventMap<Race>>();

	let chartElement: Element,
		hoverTarget: Race,
		clickTarget: Race,
		chart: d3.Selection<Element, unknown, null, undefined>,
		xScale: d3.ScaleTime<number, number, never>;

	function interactionHandler(event: MouseEvent): [Race, number, number] {
		// Calculate date for pointer coords
		let [x, y] = d3.pointer(event);
		let date = xScale.invert(x);

		// Get closest race from data
		let closestRace = races[bisectDate(races, date)];

		// Calculate distance and return tuple
		return [
			closestRace,
			Math.abs(closestRace.when.getTime() - date.getTime()),
			Math.abs(scatterY - y)
		];
	}

	function createScatterInteractDetails(rect: DOMRect, race: Race)
		: ScatterInteractDetails<Race> 
	{
		return {
			coords: { 
				x: rect.x + xScale(race.when), 
				y: rect.y + scatterY, 
				radius: scatterRadius
			},
			data: race,
		};
	}

	function mouseClickHandler(event: MouseEvent) {
		let [closestRace, xDistance, yDistance] = interactionHandler(event);

		// Send event
		if (xDistance > msInWeek || yDistance > 10 || closestRace == clickTarget) {
			clickTarget = null;
			dispatch("clear");
		}
		else if (closestRace != clickTarget) {
			clickTarget = closestRace;
			dispatch("select", createScatterInteractDetails(chartElement.getBoundingClientRect(), closestRace));
		}
	}

	function mouseMoveHandler(event: MouseEvent) {
		let [closestRace, xDistance, yDistance] = interactionHandler(event);

		// Send event
		if (xDistance > msInWeek || yDistance > 10) {
			hoverTarget = null;
			dispatch("exit");
		}
		else if (closestRace != hoverTarget) {
			hoverTarget = closestRace;
			dispatch("enter", createScatterInteractDetails(chartElement.getBoundingClientRect(), closestRace));
		}
	}

	// Create chart
	$: if (!chart && chartElement) {
		chart = createChart(chartElement, width, height, left, right, top, bottom);
		let svg = chart.select("svg");
		let clipPath = chart.select("clipPath").attr("id");

		svg.on("click", mouseClickHandler);
		svg.on("mousemove", mouseMoveHandler);

		svg.append("g")
			.attr("class", "xaxis")
			.attr("transform", `translate(${left}, ${height + top})`)
			.append("path")
			.attr("stroke", "currentColor")
			.attr("d", (_): any => {
				var path = d3.path();
				path.moveTo(0.5, 0.5);
				path.lineTo(width + 0.5, 0.5);
				path.closePath();
				return path;
			});

		svg.append("g")
			.attr("class", "scatter")
			.attr("clip-path", clipPath)
			.attr("transform", `translate(${left}, ${top})`);
	}

	// Update chart
	$: if (chart && races) {
		// Calculate X scale
		let year = races[0].when.getFullYear();
		let start = new Date(year, 0, 1);
		let end = new Date(year + 1, 0, 1);

		xScale = d3.scaleTime()
			.domain([start, end])
			.range([gap, width - gap]);

		// Create axis
		chart.select(".xaxis")
			.call(d3
				.axisBottom(xScale)
				.tickSize(16)
				.tickFormat((val: Date) => {
					return luxon.DateTime.fromJSDate(val).toFormat("LLL");
				})
			);
		
		// Move text to center
		chart.select(".xaxis")
			.selectAll<SVGTextElement, Date>("text")
			.attr("transform", (val, idx) => {
				let x = getDateTickWidth(val, idx, xScale);
				return `translate(${x/2}, 0)`;
			});
		
		// Add color bars
		chart.select(".xaxis")
			.selectAll<SVGGElement, Date>("g")
			.insert("rect",":first-child")
			.attr("class", (val) => `color-${months[val.getMonth()]}`)
			.attr("fill", "currentColor")
			.attr("x", 0)
			.attr("height", 8)
			.attr("width", (val, idx) => getDateTickWidth(val, idx, xScale));

		// Add scatter plot
		let ele = chart.select(".scatter")
			.selectAll("circle")
			.data(races) as d3.Selection<SVGCircleElement, Race, d3.BaseType, unknown>;

		// Plot points
		ele.enter()
			.append("circle")
			.merge(ele)
			.attr("r", scatterRadius)
			.attr("cx", d => xScale(d.when))
			.attr("cy", scatterY);

		// Remove old points
		ele.exit().remove();
	}
</script>

<style type="text/scss">
	#season-chart {
		:global(.xaxis) {
			font-size: 14px;
			color: black;

			:global(g:last-child) {
				visibility:hidden;
			}
		}

		:global(.scatter) {
			fill:#343434;
		}

		&.interact {
			cursor:pointer;
		}
	}
</style>

<div id="season-chart" class:interact={hoverTarget} bind:this={chartElement}></div>