<script lang="ts">
	import type { DateTime } from "luxon";
	import type { Race, ScatterCoords, ScatterInteractEvent } from "+/modules/types";

	import { storedDb as db } from "+/stores/datastore";
	import { getRaces } from "+/modules/queries/races";
	import { formatDate } from "+/modules/shared";
	import SectionTemplate from "+/components/SectionTemplate.svelte";
	import ScatterTooltip from "+/components/ScatterTooltip.svelte";
	import SeasonChart from "+/components/SeasonChart.svelte";
	import SeasonMap from "+/components/SeasonMap.svelte";

	export let params: { year: number };

	let races: Race[], 
		firstDay: DateTime, 
		lastDay: DateTime,
		focusedRace: ScatterInteractEvent<Race>,
		tooltipFlip: boolean,
		tooltipText: string,
		tooltipCoords: ScatterCoords;

	function updateData() {
		races = getRaces($db, params.year);

		firstDay = luxon.DateTime.fromJSDate(races[0].when)
			.minus({ days: 2 });

		lastDay = luxon.DateTime.fromJSDate(races[races.length - 1].when);
	}

	function chartOnSelect(event: ScatterInteractEvent<Race>) {
		focusedRace = event;
	}

	function chartOnClear() {
		focusedRace = null;
		chartOnExit();
	}

	function chartOnEnter(event: ScatterInteractEvent<Race>) {
		tooltipFlip = event.detail.data.when.getMonth() < 4;
		tooltipText = `${event.detail.data.location}, ${event.detail.data.country} - ${formatDate(event.detail.data.when, "d LLL 'at' h:mm a")}`;
		tooltipCoords = event.detail.coords;
	}

	function chartOnExit() {
		if (!focusedRace) tooltipCoords = null;
		else chartOnEnter(focusedRace);
	}

	$: if (params.year) updateData();
</script>

<style type="text/scss">
</style>

<SectionTemplate>
	<svelte:fragment slot="header">
		{params.year} Season, 
		{firstDay && formatDate(firstDay, "d LLLL yyyy")} - 
		{lastDay && formatDate(lastDay, "d LLLL yyyy")}
	</svelte:fragment>
	<ScatterTooltip content={tooltipText} flip={tooltipFlip} coords={tooltipCoords} />
	<SeasonChart {races} 
		on:select={chartOnSelect} on:clear={chartOnClear} 
		on:enter={chartOnEnter} on:exit={chartOnExit} 
	/>
	<SeasonMap {races} focusedRace={focusedRace?.detail.data} />
</SectionTemplate>
